.header-mobile {
  display: none;
  @media screen and (max-width: 800px) {
    display: block;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 10vh;
      #hamburger {
        cursor: pointer;
      }
    }
  }
}

.menu-mobile {
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3000;
  transition: all 0.3s;
  display: none;
}

.menu-content {
  position: absolute;
  top: 50%;
  left: 50%;

  width: 75%;
  height: 60vh;
  padding: 1em 0;
  transform: translate(-50%, -50%);
  background-color: hsl(0, 0%, 98%);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  #close {
    align-self: flex-end;
    margin-right: 3em;
  }
  hr {
    display: none;
  }
  li {
    cursor: pointer;
    a {
      text-decoration: none;
      color: hsl(233, 26%, 24%);

      &:hover {
        color: hsl(136, 65%, 51%);
      }
    }
  }
}
