.section-3 {
  padding: 6em 0;
  background-color: hsl(0, 0%, 98%);
  &__body {
    padding: 1em;
  }

  .column {
    display: flex;
    flex-direction: column;
    background-color: white;
  }
  h2 {
    font-size: 4rem;
    color: hsl(233, 26%, 24%);
    margin-bottom: 0.5em;
    @media screen and (max-width: 800px) {
      text-align: center;
    }
  }

  span {
    font-size: 1.2rem;
    color: hsl(233, 8%, 62%);
    display: inline-block;
    margin-bottom: 0.5em;
  }
  h4 {
    font-size: 2rem;
    color: hsl(233, 26%, 24%);
    margin-bottom: 0.5em;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      color: hsl(136, 65%, 51%);
    }
  }
  p {
    color: hsl(233, 8%, 62%);
  }
}
