html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-size: 1.8rem;
  font-family: "Public Sans";
}

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

:focus {
  outline: none;
}

button {
  cursor: pointer;
  border: 0;
}

figure {
  display: block;
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
  object-fit: cover;
}
