/* layout
------------------------------------------------------------*/
main {
  overflow: hidden;
  position: relative;
}
header {
  position: relative;
  z-index: 1000;
  background-color: white;
  .container {
    height: 10vh;
  }
  @media screen and (max-width: 800px) {
    display: none;
  }
}
.header__menu {
  display: flex;
  list-style: none;
  align-self: stretch;
  li {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 90% 10%;
    align-content: center;
    transition: all 0.2s;
    &:not(:last-child) {
      margin-right: 2em;
    }
    a {
      text-decoration: none;
      color: hsl(233, 8%, 62%);
      align-self: center;
    }

    &:hover {
      a {
        color: black;
      }
      hr {
        display: block;
      }
    }

    hr {
      display: none;
      content: "";
      border: 0;
      width: 100%;
      height: 3px;
      background-image: linear-gradient(
        to right,
        hsl(136, 65%, 51%),
        hsl(192, 70%, 51%)
      );
      align-self: end;
    }
  }
}

footer {
  background-color: hsl(233, 26%, 24%);
  padding: 2em 0;
  @media screen and (max-width: 800px) {
    .container.flex {
      flex-direction: column;
      text-align: center;
      row-gap: 1em;
    }
  }
  .column:nth-of-type(1) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    row-gap: 2em;
    figure:nth-of-type(2) {
      display: flex;
      img {
        margin-right: 0.5em;
      }
    }
  }
  .column:nth-of-type(3),
  .column:nth-of-type(2) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    row-gap: 0.5em;
    span {
      color: hsl(220, 16%, 96%);
      cursor: pointer;
      transition: all 0.2s;
      &:hover {
        color: hsl(136, 65%, 51%);
      }
    }
  }

  .column:nth-of-type(4) {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    row-gap: 1em;
    @media screen and (max-width: 800px) {
      align-items: center;
    }
    p {
      color: hsl(233, 8%, 62%);
    }
  }
}
