.section-2 {
  padding: 6em 0;
  background-color: hsl(220, 16%, 96%);
  .container {
    @media screen and (max-width: 800px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
  h2 {
    font-size: 4rem;
    color: hsl(233, 26%, 24%);
    margin-bottom: 1em;
  }
  p {
    color: hsl(233, 8%, 62%);
    line-height: 1.4;
    margin-bottom: 1em;
  }

  h3 {
    font-size: 3rem;
    color: hsl(233, 26%, 24%);
  }

  &__heading {
    width: 50%;
    margin-bottom: 3em;
  }

  .column {
    h3,
    figure {
      margin-bottom: 1.5em;
    }
    p {
      margin-bottom: 0;
    }
    figure {
      @media screen and (max-width: 800px) {
        img {
          display: inline-block;
        }
      }
    }
  }
}
