.section-1 {
  position: relative;

  background-color: hsl(0, 0%, 98%);

  @media screen and (max-width: 1331px) {
    padding: 5em 0;
    padding-top: 20em;
  }
  .container {
    height: 90vh;
    @media screen and (max-width: 1331px) {
      justify-content: center;
      align-items: flex-end;
    }
  }

  &__left {
    width: 33%;
    @media screen and (max-width: 1331px) {
      width: 65%;
      text-align: center;
    }

    h1 {
      color: hsl(233, 26%, 24%);
      font-size: 5rem;
      margin-bottom: 0.5em;
    }
    p {
      color: hsl(233, 8%, 62%);
      line-height: 1.4;
      margin-bottom: 1em;
    }
  }

  &__right {
    position: absolute;
    top: -35%;
    right: -25%;
    display: grid;
    justify-content: center;
    picture,
    & > img {
      grid-row: 1/-1;
      grid-column: 1/-1;
    }

    & > img {
      justify-self: center;
      align-self: end;
      transform: translateY(2%);

      @media screen and (max-width: 1338px) {
        transform: translateY(-28%);
      }
    }

    @media screen and (max-width: 1331px) {
      top: -7%;
      right: 8%;
    }
    @media screen and (max-width: 800px) {
      top: 0%;
      right: 0%;
    }
  }
}
