.container {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;

  @media screen and (max-width: 900px) {
    width: 90%;
  }
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.w-50 {
  width: 50%;
}

.row {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2em;
  @media screen and (max-width: 970px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 800px) {
    display: flex;
    flex-direction: column;
  }
}

.flex-1 {
  flex: 1;
}

.flex-05 {
  flex: 0.5;
  @media screen and (max-width: 800px) {
    flex: 1;
  }
}

.flex-2 {
  flex: 2;
  @media screen and (max-width: 800px) {
    flex: 1;
  }
}

.open {
  display: block;
  transition: all 0.2s;
}
